@import 'https://fonts.googleapis.com/css?family=Asap:400,700';
@import 'https://fonts.googleapis.com/css?family=Satisfy';
html, body {
  font-family: 'Asap', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  height: 100%;
}

html {
  position: relative;
}

body {
  background-color: #fff;
  color: #333333;
}

#react-content,
div[data-reactroot] {
  display: inline;
}

.text-cursive {
  font-family: 'Satisfy', cursive;
}

.fill-x {
  width: 100%;
}

.fill-y {
  height: 100%;
}

.fill-xy {
  height: 100%;
  width: 100%;
}

.psn-static {
  position: static;
}

.psn-relative {
  position: relative;
}

.psn-absolute {
  position: absolute;
}

.psn-fixed {
  position: fixed;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1;
  color: inherit;
}

h1 small,
h1 .small, h2 small,
h2 .small, h3 small,
h3 .small, h4 small,
h4 .small, h5 small,
h5 .small, h6 small,
h6 .small,
.h1 small,
.h1 .small, .h2 small,
.h2 .small, .h3 small,
.h3 .small, .h4 small,
.h4 .small, .h5 small,
.h5 .small, .h6 small,
.h6 .small {
  font-weight: normal;
  line-height: 1;
  color: #777777;
}

h1, .h1 {
  font-size: 36px;
}

h2, .h2 {
  font-size: 30px;
}

h3, .h3 {
  font-size: 24px;
}

h4, .h4 {
  font-size: 18px;
}

h5, .h5 {
  font-size: 14px;
}

h6, .h6 {
  font-size: 12px;
}

.lead {
  font-size: 16px;
  font-weight: 300;
  line-height: 1.4;
}

@media (min-width: 768px) {
  .lead {
    font-size: 21px;
