/*
|-------------------------------------------------------------------------------------
| Drawer Base
|-------------------------------------------------------------------------------------
|
|
*/
.drawer {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 1300;
}

.drawer .drawer-content {
  position: absolute;
  top: 0;
  left: -266px;
  z-index: 1;
  height: 100% !important;
  width: 256px !important;
  background-color: #fff;
  overflow: auto;
  -webkit-transition: left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, right 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -o-transition: left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, right 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: left 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms, right 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  -webkit-box-shadow: rgba(0, 0, 0, 0.156863) 0px 3px 10px, rgba(0, 0, 0, 0.227451) 0px 3px 10px;
  box-shadow: rgba(0, 0, 0, 0.156863) 0px 3px 10px, rgba(0, 0, 0, 0.227451) 0px 3px 10px;
}

.drawer.in .drawer-content {
  left: 0;
}

.drawer-right {
  left: auto;
  right: 0;
}

.drawer-right .drawer-content {
  left: auto;
  right: -266px;
}

.drawer-right.in .drawer-content {
  left: auto;
  right: 0;
}

.drawer-open {
  overflow: hidden;
}

.drawer-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: auto;
  background-color: #000;
  opacity: 0.5;
  filter: alpha(opacity=50);
}

.drawer-backdrop.in {
  opacity: 0.5;
  filter: alpha(opacity=50);
}

.drag-target {
  height: 100%;
  width: 10px;
  position: fixed;
  top: 0;
  z-index: 1001;
}

#sidenav-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 120vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  will-change: opacity;
}

/*
|------------------------------------------------------------------------------------------------------
| Offcanvas Base
|------------------------------------------------------------------------------------------------------
|
|
*/
.offcanvas {
  position: relative;
  min-height: 100%;
}

@media (min-width: 768px) {
  .offcanvas .container {
    width: auto;
  }
}

@media (min-width: 992px) {
  .offcanvas .container {
    width: auto;
  }
}

@media (min-width: 1200px) {
  .offcanvas .container {
    max-width: 1170px;
    width: auto;
  }
}

.visible-offcanvas-left-open,
.visible-offcanvas-right-open,
.visible-inline-offcanvas-left-open,
.visible-inline-offcanvas-right-open,
.visible-inline-block-offcanvas-left-open,
.visible-inline-block-offcanvas-right-open {
  display: none !important;
}

.offcanvas-left-open .hidden-offcanvas-left-open {
  display: none !important;
}

.offcanvas-left-open .visible-offcanvas-left-open {
  display: block !important;
}

.offcanvas-left-open .visible-inline-offcanvas-left-open {
  display: inline !important;
}

.offcanvas-left-open .visible-inline-block-offcanvas-left-open {
  display: inline-block !important;
}

.offcanvas-right-open .hidden-offcanvas-right-open {
  display: none !important;
}

.offcanvas-right-open .visible-offcanvas-right-open {
  display: block !important;
}

.offcanvas-right-open .visible-inline-offcanvas-right-open {
  display: inline !important;
}

.offcanvas-right-open .visible-inline-block-offcanvas-right-open {
  display: inline-block !important;
}

/*
|------------------------------------------------------------------------------------------------------
| Offcanvas Body
|------------------------------------------------------------------------------------------------------
|
|
*/
.offcanvas-body {
  position: relative;
  z-index: auto;
  height: 100%;
  min-height: 100%;
  padding: 0;
}

.offcanvas-body .navbar table {
  width: 100%;
}

.offcanvas-body .navbar-toggle {
  margin: 8px 10px;
}

@media (max-width: 767px) {
  .offcanvas-body .navbar .row > [class^='col-'],
  .offcanvas-body .navbar .row > [class*='col-'] {
    min-height: 0;
  }
}

.offcanvas.peep-right > .offcanvas-body {
  padding-right: 50px;
}

.offcanvas.peep-right > .offcanvas-body .navbar-fixed-top {
  right: 50px;
}

.offcanvas-right-open .offcanvas.peep-right > .offcanvas-body {
  /* Mobile only */
}

.offcanvas-right-open .offcanvas.peep-right > .offcanvas-body {
  padding-right: 200px;
}

@media (max-width: 767px) {
  .offcanvas-right-open .offcanvas.peep-right > .offcanvas-body {
    left: -200px;
    padding-right: 0;
  }
}

.offcanvas-right-open .offcanvas.peep-right > .offcanvas-body .navbar-fixed-top {
  right: 200px;
}

/*
|------------------------------------------------------------------------------------------------------
| Offcanvas Side
|------------------------------------------------------------------------------------------------------
|
|
*/
.offcanvas-right,
.offcanvas-left {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  min-height: 100%;
  padding: 0;
  background: #fff;
  -webkit-box-shadow: rgba(0, 0, 0, 0.156863) 0px 3px 10px, rgba(0, 0, 0, 0.227451) 0px 3px 10px;
  box-shadow: rgba(0, 0, 0, 0.156863) 0px 3px 10px, rgba(0, 0, 0, 0.227451) 0px 3px 10px;
}

.peep-left .offcanvas-left {
  display: block;
}

.offcanvas-right {
  position: absolute;
  left: auto;
  right: 0;
}

.peep-right .offcanvas-right {
  display: block;
}

.offcanvas-fixed .offcanvas-left,
.offcanvas-fixed .offcanvas-right {
  position: fixed;
  min-height: 0;
  height: 100%;
}

/*
|------------------------------------------------------------------------------------------------------
| Offcanvas Sidebar
|------------------------------------------------------------------------------------------------------
|
|
*/
.peep-left .offcanvas-sidebar,
.peep-right .offcanvas-sidebar {
  width: 50px;
}

.offcanvas-left-open .offcanvas-left .offcanvas-sidebar,
.offcanvas-right-open .offcanvas-right .offcanvas-sidebar {
  width: 200px;
}

.offcanvas-left-open .offcanvas-fixed .offcanvas-left .offcanvas-sidebar,
.offcanvas-right-open .offcanvas-fixed .offcanvas-right .offcanvas-sidebar {
  position: relative;
  height: 100%;
}

.offcanvas-sidebar .navbar-nav {
  margin: 0;
}

.offcanvas-sidebar .navbar-nav.navbar-left {
  float: left;
}

.offcanvas-sidebar .navbar-nav.navbar-right {
  float: right;
}

.offcanvas-sidebar .navbar-nav > li {
  float: left;
}

.offcanvas-sidebar .navbar-nav > li > a {
  padding-left: 10px;
  padding-right: 10px;
}
